	module.exports = {"data":{"markdownRemark":{"html":"<h2>Iframes</h2>\n<p>The HTML <code>&#x3C;iframe></code> element represents an inline frame, which allows you to include an independent HTML document into the current HTML document. The <code>&#x3C;iframe></code> is typically used for embedding third-party media, your own media, widgets, code snippets, or embedding third-party applets such as payment forms.</p>\n<h3>Attributes</h3>\n<p>Listed below are some of the <code>&#x3C;iframe></code>’s attributes:</p>\n<table>\n<thead>\n<tr>\n<th>Attribute</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>allowfullscreen</code></td>\n<td>Set to true to allow the frame to be placed into full screen mode</td>\n</tr>\n<tr>\n<td><code>frameborder</code></td>\n<td>Tells the browser to draw a border around the frame (set to 1 by default)</td>\n</tr>\n<tr>\n<td><code>height</code></td>\n<td>The height of the frame in CSS pixels</td>\n</tr>\n<tr>\n<td><code>name</code></td>\n<td>A name for the frame</td>\n</tr>\n<tr>\n<td><code>src</code></td>\n<td>The URL of the web page to embed</td>\n</tr>\n<tr>\n<td><code>width</code></td>\n<td>The width of the frame in CSS pixels</td>\n</tr>\n</tbody>\n</table>\n<h3>Examples</h3>\n<p>Embedding a YouTube video with an <code>&#x3C;iframe></code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>iframe</span> <span class=\"token attr-name\">width</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>560<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">height</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>315<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://www.youtube.com/embed/v8kFT4I31es<span class=\"token punctuation\">\"</span></span> \n<span class=\"token attr-name\">frameborder</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>0<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">allowfullscreen</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>iframe</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Embedding Google Maps with an <code>&#x3C;iframe></code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>iframe</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://www.google.com/maps/embed?pb<span class=\"token punctuation\">=</span>!1m18!1m12!1m3!1d774386.2436462595!2d-74.53874786161381!3d40.69718109704434!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c24fa5d33f083b%3A0xc80b8f06e177fe62!2sNew+York%2C+NY%2C+USA!5e0!3m2!1sen!2sau!4v1508405930424<span class=\"token punctuation\">\"</span></span> \n<span class=\"token attr-name\">width</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>600<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">height</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>450<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">frameborder</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>0<span class=\"token punctuation\">\"</span></span><span class=\"token style-attr language-css\"><span class=\"token attr-name\"> <span class=\"token attr-name\">style</span></span><span class=\"token punctuation\">=\"</span><span class=\"token attr-value\"><span class=\"token property\">border</span><span class=\"token punctuation\">:</span>0</span><span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">allowfullscreen</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>iframe</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h3>Alternative Text</h3>\n<p>The content between the opening and closing <code>&#x3C;iframe></code> tags is used as alternative text, to be displayed if the viewer’s browser does not support iframes.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>iframe</span> <span class=\"token attr-name\">width</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>560<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">height</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>315<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://www.youtube.com/embed/v8kFT4I31es<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">frameborder</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>0<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>Your browser does not support iframes.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>iframe</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h3>Targeting an Iframe in a Link</h3>\n<p>Any <code>&#x3C;a></code> link can target the content of an <code>&#x3C;iframe></code> element. Rather than redirect the browser window to the linked webpage, it will redirect the <code>&#x3C;iframe></code>. For this to work, the <code>target</code> attribute of the <code>&#x3C;a></code> element must match the <code>name</code> attribute of the <code>&#x3C;iframe></code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>iframe</span> <span class=\"token attr-name\">width</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>560<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">height</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>315<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>about:blank<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">frameborder</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>0<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>iframe-redir<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>iframe</span><span class=\"token punctuation\">></span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span> <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://www.youtube.com/embed/v8kFT4I31es<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">target</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>iframe-redir<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>Redirect the Iframe<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>This example will show a blank <code>&#x3C;iframe></code> initially, but when you click the link above it will redirect the <code>&#x3C;iframe></code> to show a YouTube video.</p>\n<h3>Javascript and Iframes</h3>\n<p>Documents embedded in an <code>&#x3C;iframe></code> can run JavaScript within their own context (without affecting the parent webpage) as normal.</p>\n<p>Any script interaction between the parent webpage and the content of the embedded <code>&#x3C;iframe></code> is subject to the same-origin policy. This means that if you load the content of the <code>&#x3C;iframe></code> from a different domain, the browser will block any attempt to access that content with JavaScript.</p>\n<h3>More Information:</h3>\n<p>See the <a href=\"https://developer.mozilla.org/en-US/docs/Web/HTML/Element/iframe\">MDN Web Docs</a>.</p>","fields":{"slug":"/html/iframes/"},"frontmatter":{"title":"Iframes"}}},"pathContext":{"slug":"/html/iframes/"}}

